// Video container styling
.video-container {
  position: relative;
  padding-bottom: 56.25%;
  height: 0;
  overflow: hidden;
  border-radius: 12px;
  box-shadow: 0 8px 24px rgba(15, 23, 42, 0.08);
  margin-bottom: 20px;

  iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border: none;
  }
}

// Video title styling
.video-title {
  font-weight: 600;
  font-size: 1.05rem;
  margin: 0 0 8px 0;
  line-height: 1.4;
}

// Video description styling
.video-description {
  font-size: 0.95rem;
  line-height: 1.6;
  color: var(--text-light);
  margin: 0;
}
